const cardToggle = function(){
    const header = $('.card__head--toggle-mobile');
    const card = $('.card--toggle-mobile');
    const cardInner = $('.card__inner--toggle-mobile');
    const headerActiveClassName = 'card__head--toggle-mobile-active';
    const headerClassName = 'card__head--toggle-mobile';
    const cardClassName = 'card';
    const innerClassName = 'card__inner';

    return {
        actions: function(){
            header.on('click', function(){
                if($(window).outerWidth() > 720) return
                $(this).toggleClass(headerActiveClassName);
                $(this).parent(`.${cardClassName}`).find(`.${innerClassName}`).toggle('slow');
            })
        },
        resize: function(){
          $(window).on('resize', function(){
              cardInner.hide();
              if ( $(window).outerWidth() < 720 ) {

              card.each(function(){
                  if ($(this).find(`.${headerClassName}`).hasClass(headerActiveClassName)){
                    $(this).find(`.${innerClassName}`).show();
                  }
              });
              } else {
                cardInner.show();
              }
          })
        },

        alwaysToggle(){
          $('.card__head--toggle').on('click', function(){
            $(this).toggleClass('card__head--toggle-active');
            $(this).closest('.card').find('.card__inner').toggle('slow');
          });

          $('.card__head--toggle').each(function(){
            $(this).closest('.card').find('.card__inner').hide();
            if( $(this).hasClass('card__head--toggle-active')){
              $(this).closest('.card').find('.card__inner').show();
            }
          })
        },
        initialMobileState: function(){

          if($(window).outerWidth() < 720) {
            $('.card__head--toggle-mobile').each(function(){
              $(this).closest('.card').find('.card__inner').hide();
              if( $(this).hasClass('card__head--toggle-mobile-active')){
                $(this).closest('.card').find('.card__inner').show();
              }
            })
          }

        },
        init: function(){
            this.actions();
            this.resize();
            this.alwaysToggle()
            this.initialMobileState()

        }
    }
};

export default cardToggle


