import Vue from 'vue'

export default {
  name: 'OrderRow',
  data() {
    return {
      state: 'online'
    }
  },
  props: ['item',],
  computed: {
    maxRate: function () {
      return Number(this.item.max_amount).toFixed(2)
    },
    minRate: function () {
      return Number(this.item.min_amount).toFixed(2)
    },
    profit: function () {
      return Number(this.item.profit).toFixed(2)
    },
    rate: function () {
      return Number(this.item.rate).toFixed(2)
    }
  },
  template: `
          <div class="i-tab__row">
            <div class="i-tab__cell online">
              <p class="i-tab__prod-name" v-if="item.user">{{item.user.username}}<span class="dot"></span></p>
              <div class="i-tab__rating">
                <div class="rating">
                  <p class="rating__title">Рейтинг</p> 
                  <div class="rating__bar">
                    <div 
                      v-bind:class="{
                        rating__progress: true,
                        abs: item.rate >= 90,
                        conf: item.rate >= 70 && item.rate < 90,
                        mid: item.rate >= 40 && item.rate < 70,
                        danger: item.rate < 40,
                        }"
                      v-bind:style="'width: ' + item.rate + '%'" >{{item.rate}}</div>
                  </div>
                </div>
              </div>
            </div>
  
            <div class="i-tab__cell">
              <p v-for="processor in item.processors" class="i-tab__payment">{{processor.name}}</p>
            </div>
            
            <div class="i-tab__cell">
              <p class="i-tab__price-usd">{{rate}}&nbsp<span>{{item.currency.name}}</span></p>
              <p class="i-tab__price-hint">Наценка</p>
              <p class="i-tab__price-markup">{{profit}}%</p>
            </div>
            
            <div class="i-tab__cell">
              <p class="i-tab__limit">{{minRate}} - {{maxRate}} USD</p>
            </div>
            
            <div class="i-tab__cell">
              <div class="i-tab__buy"><a class="l-btn l-btn--secondary i-tab__buy-btn" :href=item.url>Купить
              </a></div>
            </div>
          </div>
      `
}



