import Vue from 'vue'
import OrdersSearchResult from '../../js/vue/components/OrdersSearchResult'

$(document).ready(function () {
  if(document.querySelector('#OrdersSearchResultApp')){
    let app = new Vue({
      el: '#OrdersSearchResultApp',
      render: h => h(OrdersSearchResult)
    });
  }
});



