import PerfectScrollbar from 'perfect-scrollbar'

function Header() {

    const mobileMenu = $('#js-mobile-menu');
    const mobileMenuBtn = $('#js-mobile-menu-btn');
    const userMenu = $('#js-header-user-menu');
    const userMenuBtn = $('#js-header-user-menu-btn');

    return {
        close: function(){
            $(window).on('click', function(){
                $('.header__drop').hide();
            })
        },

        userMenu: function(){
            let self = this;
            userMenuBtn.on('click', function(event){
                event.stopPropagation();
                userMenu.toggle();
                // self.closeDrops(this);
                $(window).trigger('close-another-drops', this);
            });
        },

        mobileMenu: function () {
            if (!mobileMenu.length || !mobileMenuBtn.length ) return

            mobileMenuBtn.on('click', function(event){
                event.stopPropagation();

                $(window).trigger('close-another-drops')

                if($(window).outerWidth() < 399) {
                    $('.l-page').toggleClass('l-page--noscroll')
                }
                $(this).toggleClass('burger--active');
                mobileMenu.slideToggle(275)
            });

            mobileMenu.on('click', function(event){
                event.stopPropagation();
                mobileMenu.show();
            });

            $(window).on('click resize', function(){
                mobileMenu.slideUp('275');
                mobileMenuBtn.removeClass('burger--active');
                $('.l-page').removeClass('l-page--noscroll');

            })
        },

        notification: function(){
            let self = this;
            $('#js-header-notifications').on('click', function(event) {
                event.stopPropagation();
                self.notificationPosition();
                $('#js-header-notifications-drop').toggle();
                $(window).trigger('close-another-drops', this);
                mobileMenu.slideUp('275');
                mobileMenuBtn.removeClass('burger--active');
                $('.l-page').removeClass('l-page--noscroll');

            });

            $(window).on('resize', ()=> {
                this.notificationPosition();
            })
          if (document.querySelector('#js-notification-scroll')) {
            const ps = new PerfectScrollbar('#js-notification-scroll');
          }
        },
        notificationPosition: function(){
          if($(window).outerWidth() < 600){
            const containerPosition = $('#js-header-notifications')[0].getBoundingClientRect();
            const dropWidth = $(window).innerWidth() - 6;
            $('#js-header-notifications-drop').css({"left": -containerPosition.x + 3, "max-width": dropWidth, "width": dropWidth})

            $('#notifications-drop-triangle').css({'left': containerPosition.x, 'right': 'unset'})
          } else {
            $('#js-header-notifications-drop').attr('style', '');
            $('#notifications-drop-triangle').attr('style', '')
          }

        },


        closeDrops: function (elem) {
            if( !elem ) {
                $('.js-header-drop').hide()
            }

            $('.js-header-drop-container').each(function(){
                if (elem === this) {
                    return
                }
                $(this).find('.js-header-drop').hide();
            });
            $('.header-mobile-menu__item-link').on('click', function () {
              mobileMenu.hide();
              mobileMenuBtn.removeClass('burger--active');
              $('.l-page').removeClass('l-page--noscroll');

            })
            // mobileMenu.slideUp('275');
            // mobileMenuBtn.removeClass('burger--active');
            // $('.l-page').removeClass('l-page--noscroll');
        },
        init: function(){
            this.userMenu();
            this.mobileMenu();
            if(document.querySelector('#js-header-notifications')){
              this.notification();
              this.notificationPosition()
            }
        }
    }
}

export default Header


