import Vue from "vue";
import DatePicker from "vue2-datepicker";
// import Datepicker from 'vuejs-datepicker';
import axios from "axios";
import queryString from "query-string";
import AppLoader from "./AppLoader"

import PerfectScrollbar from "perfect-scrollbar";

import { transactionsUrl } from "../api";

export default Vue.component("TransactionsApp", {
  name: "TransactionsApp",
  data() {
    return {
      data: [],
      date: [],
      filterState: "all",
      loading: false,
      total: 0,
      counter: 0,
      errored: false,
      limit: 5,
      dataPickerLang: {
        days: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
        months: [
          "Январь",
          "Февраль",
          "Март",
          "Апрель",
          "Май",
          "Июнь",
          "Июль",
          "Август",
          "Сентябрь",
          "Октябрь",
          "Ноябрь",
          "Декабрь"
        ],
        pickers: [
          "След. 7 дней",
          "След. 30 дней",
          "Пред. 7 дней",
          "Пред. 30 дней"
        ],
        placeholder: {
          date: "Выберите дату",
          dateRange: "Выберите период"
        }
      }
    };
  },
  components: {
    DatePicker: DatePicker,
    AppLoader: AppLoader,
  },
  methods: {
    choseDateHandler: function(date) {
      this.date = date;
    },
    getData: function() {
      this.loading = true;
      this.data = [];
      this.page = 1;
      const queryParam = {
        filter: this.filter,
        date: this.date,
        page: this.page,
        limit: this.limit
      };
      axios
        .get(`${transactionsUrl}?${queryString.stringify(queryParam)}`)
        .then(response => {
          this.data = response.data.items;
          this.counter = response.data.items.length;
          this.total = response.data.total;
        })
        .catch(error => {
          this.errored = true;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getMoreData: function() {
      this.loading = true;
      const queryParam = {
        filter: this.filter,
        date: this.date,
        page: this.page,
        limit: this.limit
      };
      axios
        .get(`${transactionsUrl}?${queryString.stringify(queryParam)}`)
        .then(response => {
          this.data = [...this.data].concat(response.data.items);
          this.counter = this.data.length;
          this.total = response.data.total;
        })
        .catch(error => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    }
  },
  computed: {
    filter: function() {
      switch (this.filterState) {
        case "all":
          return 1;
        case "sent":
          return 2;
        case "received":
          return 3;
      }
    }
  },
  watch: {
    page: function() {
      if (page !== 1) {
        this.getMoreData();
      }
    },
    filter: function(){
      this.getData();
    },
    date: function () {
      this.getData();
    }
  },
  mounted: function(){
    this.getData();
  },
  template: `
   <div class="card card--footer transactions">
      <div class="card__head">
        <div class="card__title-wrap">
          <h3 class="card__title">Транзакции кошелька</h3>
        </div>
      </div>
      <div class="card__inner">
        <div class="transactions__filter">
          <div class="transactions__filter-tabs">
            <div class="transactions__filter-tab">
              <div 
                :class="['l-tab l-tab--filter', {active: this.filterState === 'all'}]" 
                v-on:click="filterState ='all'">
                <span>Все</span>
              </div>
            </div>
            <div class="transactions__filter-tab">
              <div 
                :class="['l-tab l-tab--filter', {active: this.filterState === 'sent'}]"
                v-on:click="filterState='sent'">
                <span>Отправленные</span>
              </div>
            </div>
            <div class="transactions__filter-tab">
              <div 
                :class="['l-tab l-tab--filter', {active: this.filterState === 'received'}]"
                v-on:click="filterState ='received'">
                <span>Полученные</span>
              </div>
            </div>
          </div>
          <div class="transactions__filter-interval">
            <div class="interval"><span>Период: &nbsp</span>
              <div class="interval__sel">
                <DatePicker :range=true :lang=this.dataPickerLang v-on:input="choseDateHandler"/>
              </div>
            </div>
          </div>
        </div>
        <div class="card__table">
          <div class="c-tab c-tab--transactions">

            <div class="i-tab__loader" v-if="loading">
              <AppLoader/>
            </div>
            
            <div class="i-tab__not-found" v-if="!data.length && !loading">
              <span>Транзакций нет</span>                            
            </div>
          
            <div v-if="data.length >= 1" class="c-tab__row transactions__row-desktop">
              <div class="c-tab__cell"><span>Дата</span></div>
              <div class="c-tab__cell"><span>Полученные BTC</span></div>
              <div class="c-tab__cell"><span>Отправленные BTC</span></div>
              <div class="c-tab__cell"><span>Описание</span></div>
              <div class="c-tab__cell"><span>Сумма комиссии за депозит, BTC</span></div>
            </div>
            
            <div v-for="item in data" class="transactions__row-mobile mb-m">
              <div class="mb-s">
                <p class="caption">Дата</p>
                <p>07.07.2018</p>
              </div>
              <div class="mb-s">
                <p class="caption">Полученные BTC</p>
                <p>0.15248</p>
              </div>
              <div class="mb-s">
                <p class="caption">Описание</p>
                <p>сделка с yuriknk</p>
              </div>
              <div class="mb-s">
                <p class="caption">Сумма комиссии за депозит, BTC</p>
                <p>0.0005</p>
              </div>
            </div>

            <div v-for="item in data" class="c-tab__row transactions__row-desktop">
              <div class="c-tab__cell"><span>07.07.2018</span></div>
              <div class="c-tab__cell"><span></span></div>
              <div class="c-tab__cell"><span>0.15248</span></div>
              <div class="c-tab__cell"><span>11111</span></div>
              <div class="c-tab__cell"><span>0.0005</span></div>
            </div>

            <div v-if="counter < total" class="card__btn">
              <div class="card__btn card__btn--center">
                <div class="l-btn">
                  <div class="l-btn__inner">
                    <svg class="svg-reload-more-dims color-primary">
                      <use xlink:href="#reload-more"/>
                    </svg>
                    <span>Показать больше</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  `
});



