import Modal from './modules/modal'
import Alerts from './modules/alerts'
import Header from './modules/header'
import Select from './modules/select-new'
import cardToggle from './modules/cardToggle'
import prevCard from './modules/prevCard'

// import Cabinet from './modules/cabinet'
import './modules/cardTab'
import './modules/closeHelper'
import './modules/clearInput'
import './modules/switchTab'

$(document).ready(function () {
  Modal().init();
  Select().init();
  Alerts().init();
  Header().init();
  cardToggle().init();
  prevCard().init();
  // Cabinet().init();

  $('.card__head--toggle').on('click', function () {
    var arrow = $(this).parent().find('.card__arrow');
    var inner = $(this).parent().find('.card__inner--hint');
    inner.toggle();
    arrow.toggleClass('opened');
  });
});

