// import Vue from 'vue'
import axios from "axios/index";
import Select from "../../modules/select-new"

export default {
  name: 'AppSelect',
  data() {
    return {
      items: [],
      value: '',
      erorred: false,
    }
  },

  template: `
        <div class="country"><span>Страна :</span>
          <div class="form__input-box">
            <div id="appCountry" class="form__input-sel custom-select">
              <select v-on:change="selectChange">
                <option 
                  v-for="item in items"
                  :value="item.id" 
                >
                    {{item.name}}
                </option>
              </select>
            </div>
          </div>
        </div>
    `,

  methods: {
    getCountries: function () {
      axios
        .get('http://front.bigrate.loc/api/countries')
        .then(response => {
          this.items = response.data.items;
        })
        .catch(error => {
          this.errored = true;
        })
        .finally(this.initCustomSelect);
    },
    initCustomSelect: function () {
      $('#appCountry').each(Select().createSelect);
    },
    selectChange: function (event) {
      const select = event.target;
      const data = {
        id: select.value,
        index: select.selectedIndex,
        text: select.options[select.selectedIndex].innerText,
      };

      console.dir(data);
      this.$emit('changeCountry', data)
    }
  },
  mounted() {
    const self = this;
    this.getCountries();

  }
}


