import PerfectScrollbar from "perfect-scrollbar";

function InfoPage() {
  let state;
  const s = {
    tab: 'card-tab__tab',
    tabActive: 'card-tab__tab--active',
    content: 'js-group-content',
    showFullList: 'js-show-full-list',
    hideFullList: 'js-hide-full-list',
    mainState: 'table',
    fullList: 'full-list'
  };

  return {
    switchTab: function (data) {
      let {group, elem} = data;
      $(`.${s.content}`).hide();
      $(`.${s.content}[data-group="${group}"]`).show();
      $(`.${s.tab}`).removeClass(s.tabActive);
      $(elem).addClass(s.tabActive);
      $(`.${s.content}[data-group="${s.mainState}"]`).show();
    },

    hideFullList: function () {
      $(`.${s.content}`).hide();
      this.initState();
    },

    showFullList: function () {
      $(`.${s.content}`).hide();
      $(`.${s.content}[data-group="${s.fullList}"]`).show();
    },

    fullListActions: function () {
      $(`.${s.showFullList}`).on('click', () => {
        let group = $(this).data('param');
        this.showFullList(group);
      });
      $(`.${s.hideFullList}`).on('click', () => {
        let group = $(this).data('param');
        this.hideFullList();
      });
    },
    actions: function () {
      let self = this;
      $(`.${s.tab}`).on('click', function () {
        let group = $(this).data('group');
        let data = {
          group: group,
          elem: this
        };
        self.switchTab(data);
      });
    },
    initState: function () {
      let elem = $(`.${s.tabActive}`);
      let group = elem.data('group');
      let data = {
        group: group,
        elem: elem
      };
      this.switchTab(data);
    },
    scrollTable: function () {
      if (document.querySelector('.js-c-tab--completed-deal')) {
        const ps = new PerfectScrollbar('.js-c-tab--completed-deal', {
          suppressScrollX: false,
          suppressScrollY: true,
        });
      }
    },
    init: function () {
      this.initState();
      this.actions();
      this.fullListActions();
      this.scrollTable();
    }
  }
}

$(document).ready(function () {
  InfoPage().init()
});



