import Vue from 'vue'
import OrdersApp from '../../js/vue/components/OrdersApp'

$(document).ready(function () {
  if (document.querySelector('#OrderApp')) {
    let app = new Vue({
      el: '#OrderApp',
      render: h => h(OrdersApp)
    })
  }
});


