import PerfectScrollbar from 'perfect-scrollbar'
$(document).ready(function(){
  if(document.querySelector('#js-chat-window-scroll')){
    const chatWindowScroll = new PerfectScrollbar('#js-chat-window-scroll', {
      wheelSpeed: 0.5,
    });
    // chatWindowScroll.scrollTop = 100;
    window.chatWindowScroll = chatWindowScroll;
  }
});


