import Vue from "vue";
import queryString from "query-string";
import axios from "axios";

import OrderRow from "./OrderRow";
import AppLoader from "./AppLoader";

export default Vue.component("OrdersSearchResult", {
  name: "OrdersSearchResult",
  data() {
    return {
      amount: 0,
      category: 0,
      country: 0,
      processor: 0,
      currency: 0,
      total: 0,
      counter: 0,
      loading: true,
      errored: false,
      page: 1,
      limit: 5,
      data: []
    };
  },
  components: {
    OrderRow: OrderRow,
    AppLoader: AppLoader
  },
  template: `
    <section class="orders">
      <div class="l-container l-container--full-600">
        <div class="l-container-center">
          <div class="l-inner l-inner--10">
            <div class="orders__inner">
              <div class="info-tab__table">
                <div class="card-tab__inner card-tab__inner--nopaddig-600 card-tab__inner--no-shadow-600">
                    
                    <div class="card-tab__content">
                      <!--i-tab__head-->
                      <div class="i-tab__head i-tab__row">
                        <div class="i-tab__head-cell i-tab__cell"><span>Продавец</span></div>
                        <div class="i-tab__head-cell i-tab__cell"><span>Способ оплаты</span></div>
                        <div class="i-tab__head-cell i-tab__head-cell--sort i-tab__head-cell--up i-tab__cell"><span>Цена /BTC</span></div>
                        <div class="i-tab__head-cell i-tab__cell"><span>Лимит</span></div>
                      </div>

                      <!--/end i-tab__head-->
                      <div class="i-tab__loader" v-if="loading">
                        <AppLoader/>
                      </div>
                      
                      <div class="i-tab__not-found" v-if="!data.length && !loading">
                        <span>Объявления не найдены</span>                            
                      </div>
                      
                      <!--i-tab__row-->
                      <OrderRow 
                        v-for="item in data"
                        :key="item.id"
                        :item="item"/>

                    </div>
                    
                    <div class="i-tab__footer" v-if="counter < total">
                        <button class="l-btn l-btn--arr" v-on:click="page++">
                          <div class="l-btn__inner">
                            <img src="img/icons/reload-more.png"/><span>Показать больше</span>
                          </div>
                        </button>
                    </div>
                  </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>`,
  computed: {},
  mounted() {
    this.getFormData();
    this.getData();
    $("#search-form").on("submit", event => {
      event.preventDefault();
      this.getFormData();
      this.getData();
    });
  },
  methods: {
    getData: function() {
      this.loading = true;
      this.data = [];
      this.page = 1;
      const queryParam = {
        category: this.category,
        country: this.country,
        limit: this.limit,
        processor: this.processor,
        amount: this.amount,
        page: this.page
      };
      axios
        .get(`${BASE_URL}api/orders?${queryString.stringify(queryParam)}`)
        .then(response => {
          this.data = response.data.items;
          this.counter = response.data.items.length;
          this.total = response.data.total;
          // console.log(response)
        })
        .catch(error => {
          this.errored = true;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getMoreData: function() {
      this.loading = true;

      const queryParam = {
        category: this.category,
        country: this.country,
        limit: this.limit,
        processor: this.processor,
        amount: this.amount,
        page: this.page
      };

      axios
        .get(`${BASE_URL}api/orders?${queryString.stringify(queryParam)}`)
        .then(response => {
          this.data = [...this.data].concat(response.data.items);
          this.counter = this.data.length;
        })
        .catch(error => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    getFormData() {
      const amount = $("#amount").val();
      const currency = $("#currency").val();
      const category = $("#category").val();
      const country = $("#country").val();
      const processor = $("#processor").val();

      this.amount = Number(amount);
      this.currency = Number(currency);
      this.category = Number(category);
      this.country = Number(country);
      this.processor = Number(processor);
    }
  },
  watch: {
    page: function() {
      if(this.page !== 1){
        this.getMoreData();
      }
    }
  }
});



