import TransactionsApp from '../../js/vue/components/TransactionsApp'

$(document).ready(function () {
  if (document.querySelector('#wallet-transaction-app')) {
    let app = new Vue({
      el: '#wallet-transaction-app',
      render: h => h(TransactionsApp)
    })
  }
});




