function prevCard() {
  const boxClass = '.prev-card__descr';
  return {
    ellipsizeTextBox: function (className) {
      var classList = document.querySelectorAll(className);

      // console.log(classList);

      for (var i = 0; i < classList.length; i++) {
        var el = classList[i];

        var wordArray = el.innerHTML.split(' ');
        // console.log(wordArray);

        while (el.scrollHeight > el.offsetHeight) {
          wordArray.pop();
          el.innerHTML = wordArray.join(' ') + '...';
        }
      }
    },
    resize: function () {
      $(window).on('resize', () => {
        this.ellipsizeTextBox(boxClass);
      });
    },
    init: function(){
      this.ellipsizeTextBox(boxClass);
      this.resize();
    }
  }
}

export default prevCard




