import Vue from "vue";
import axios from "axios";
import queryString from "query-string";

import PerfectScrollbar from "perfect-scrollbar";

import switchState from "./switchState";
import AppSelect from "./AppSelect";
import OrderRow from "./OrderRow";
import AppLoader from "./AppLoader";

import { ordersUrl } from "../api";

export default Vue.component("OrdersApp", {
  name: "OrdersApp",
  data() {
    return {
      transactionType: "buy",
      dealType: "online",
      data: [],
      info: null,
      loading: true,
      errored: false,
      page: 1,
      limit: 5,
      country: 0,
      counter: 0,
      total: 0
    };
  },
  components: {
    SwitchState: switchState,
    AppSelect: AppSelect,
    OrderRow: OrderRow,
    AppLoader: AppLoader
  },
  template: `
        <section class="orders">
            <div class="l-container l-container--full-600">
              <div class="l-container-center">
                  <div class="l-inner l-inner--10">
                    <div class="orders__inner">
                      <div class="orders__h-control">
                        <div class="orders__h-control-switch">
                        
                        <!--SwitchState componnet-->
                          <SwitchState v-on:switchState="dealType = $event"/>
                          
                        </div>
                        <div class="orders__h-control-country">
                          <AppSelect v-on:changeCountry="changeCountry"/>
                          
                        </div>
                      </div>
                      <div class="info-tab__table">
                        <div class="card-tab">
                            <div id="js-orderApp-card-tab__tabs-wrap">
                                <div class="card-tab__tabs">
        
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'buy'}"
                                    v-on:click="changeTransactionType('buy')">
                                    <span>Купить биткоин</span>
                                  </div>
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'sell'}"
                                    v-on:click="changeTransactionType('sell')">
                                    <span>Продать биткоин</span>
                                  </div>
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'credit'}"
                                    v-on:click="changeTransactionType('credit')">
                                    <span>Кредитование</span>
                                  </div>
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'borrow'}"
                                    v-on:click="changeTransactionType('borrow')">
                                    <span>Займ</span>
                                  </div>
                                </div>
                              </div>
                          </div>
                          
                          <div class="card-tab__inner card-tab__inner--nopaddig-600">
                            
                            <div class="card-tab__content">
        
                              <!--i-tab__head-->
                              <div class="i-tab__head i-tab__row">
                                <div class="i-tab__head-cell i-tab__cell"><span>Продавец</span></div>
                                <div class="i-tab__head-cell i-tab__cell"><span>Способ оплаты</span></div>
                                <div class="i-tab__head-cell i-tab__head-cell--sort i-tab__head-cell--up i-tab__cell"><span>Цена /BTC</span></div>
                                <div class="i-tab__head-cell i-tab__cell"><span>Лимит</span></div>
                              </div>
    
                              <!--/end i-tab__head-->
                              <div class="i-tab__loader" v-if="loading">
                                <AppLoader/>
                              </div>
                              <div class="i-tab__not-found" v-if="!data.length && !loading">
                                <span>Объявления не найдены</span>                            
                              </div>
                              
                              <!--i-tab__row-->
                                <OrderRow v-for="item in data" :key="item.id" :item="item"/>
    
                            </div>
                            
                            <div class="i-tab__footer" v-if="counter < total">
                                <button class="l-btn l-btn--arr" v-on:click="page++">
                                  <div class="l-btn__inner">
                                    <img src="img/icons/reload-more.png"/><span>Показать больше</span>
                                  </div>
                                </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
      </section>
    `,
  computed: {
    category: function() {
      if (this.dealType === "online") {
        switch (this.transactionType) {
          case "buy":
            return 3;
            break;
          case "sell":
            return 4;
            break;
          case "credit":
            return 7;
            break;
          case "borrow":
            return 8;
            break;
        }
      } else if (this.dealType === "offline") {
        switch (this.transactionType) {
          case "buy":
            return 1;
            break;
          case "sell":
            return 2;
            break;
          case "credit":
            return 5;
            break;
          case "borrow":
            return 6;
            break;
        }
      }
    }
  },
  mounted() {
    this.getData();
    this.initializeScrollBar();
  },
  methods: {
    getData: function() {
      this.data = [];
      this.loading = true;
      this.page = 1;
      const queryParam = {
        category: this.category,
        country: this.country,
        page: this.page,
        limit: this.limit
      };

      axios
        .get(
          `${ordersUrl}?${queryString.stringify(queryParam)}`
        )
        .then(response => {
          this.data = response.data.items;
          this.counter = response.data.items.length;
          this.total = response.data.total;
        })
        .catch(error => {
          this.errored = true;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getMoreData: function() {
      this.loading = true;
      const queryParam = {
        category: this.category,
        country: this.country,
        page: this.page,
        limit: this.limit
      };
      axios
        .get(
          `${ordersUrl}?${queryString.stringify(queryParam)}`
        )
        .then(response => {
          this.data = [...this.data].concat(response.data.items);
          this.counter = this.data.length;
          this.total = response.data.total;
        })
        .catch(error => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    changeTransactionType: function(data) {
      this.transactionType = data;
    },
    initializeScrollBar: function() {
      const ps = new PerfectScrollbar("#js-orderApp-card-tab__tabs-wrap", {
        suppressScrollX: false,
        suppressScrollY: true
      });
    },
    changeCountry(data) {
      this.country = data.id;
    }
  },
  watch: {
    category: function() {
      this.getData();
    },
    country: function() {
      this.getData();
    },
    page: function() {
      if(this.page !== 1){
        this.getMoreData();
      }
    }
  }
});



